@import 'index';

.about{
  @include hide(0.7s);

  &.-active{
    @include show();
  }

  & .nav{
    @media screen and (min-width: 1025px){
      display: none;
    }
  }

  &-contain{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 100%;
    max-width: 1330px;
  }

  &-startPepeha{
    position: fixed;
    bottom: 1vh;
    top: unset;
    right: 30px;
    width: 10em;
    height: 10em;
    z-index: 4;
    transform: translateY(-50%);

    @media screen and (max-width: 768px){
      width: 13em;
      height: 13em;
      max-width: 100px;
      max-height: 100px;
      position: fixed!important;
      bottom: 7vh!important;
      top: unset!important;
      left: 50% !important;
      transform: translate(-50%,0%);
    }

    @media screen and (min-width: 1330px){
      left: calc(50% + (1330px / 2));
      transform: translate(-100%,-50%);
    }
  }

  &-title{
    position: relative;
    margin: 8vh 0;
    color: $white;
    font-size: calc(18vw - 0.75vh);
    text-transform: uppercase;
    font-family: pepeha display;
    letter-spacing: 0.2em;
    font-weight: normal;
    line-height: 1;
    z-index: 2;

    @media screen and (min-width: 1330px){
      font-size: 240px;
    }
  }

  &-project{
    width: 100%;
    padding-top: 2vh;
    margin-bottom: 5vh;
    border-top: solid $beige 2px;

    &-title{
      margin: 0;
      font-size: 1.5em;
      letter-spacing: 0.12em;
      text-transform: uppercase;
      color: $beige;
    }

    &-paragraph{
      color: $white;
      margin: 0;
      margin-top: 1em;
      font-size: 2.5em;
      line-height: 1.5em;
    }
  }

  &-img{
    width: 100%;
    margin: 0.5vh;
  }

  &-smallImgs{
    width: 100%;
    display: flex;
    flex-wrap: wrap;
    flex-direction: column;
    align-items: center;
    margin-bottom: 4vh;

    @media screen and (min-width: 1025px){
      flex-direction: row;
    }
    
    &-img{
      width: 100%;
      margin: 0.5vh 0; 

      @media screen and (min-width: 1025px){
        width: calc(50% - 0.5vh);
        margin: 0.5vh 0.5vh; 
      }

      &:first-child{
        margin-left: 0;
      }

      &:last-child{
        margin-right: 0;
      }
    }

    &.-last{
      margin: 7vh 0;
      margin-bottom: 3vh;
    }

  }

  &-iconDescr{
    margin: 1vh 0;
    margin-bottom: 3vh;
    
    &-item{
      display: flex;
      flex-direction: column;
      justify-content: flex-start;
      align-items: center;
      text-decoration: none;
      width: calc(100% * (6/12) - 4em);
      margin: 2em;

      @media screen and (min-width: 1025px){
        align-items: flex-start;
        width: calc(100% * (3/12) - 3.1vw);
        margin: 0;
        margin-bottom: 4em;
      }
    }

    &-icon{
      width: 80%;
      margin-bottom: 2em;
      align-self: center;

      @media screen and (max-width: 1025px){
        max-width: 150px;
      }

      @media screen and (min-width: 1025px){
        align-self: flex-start;
      }
    }

    &-paragraph{
      color: $beige;
      font-size: 1.8em;
      text-align: center;
      letter-spacing: .02em;
      line-height: 1.4em;
      margin-bottom: 0.25em;

      @media screen and (min-width: 1025px){
        text-align: left;
      }
    }

    &-link{
      color: $beige;
      font-size: 1.6em;
      text-transform: uppercase;
      font-family: andSansBold;
      border-bottom: solid 2px $beige;
      margin-top: 1em;
      letter-spacing: 0.12em;
    }
  }
}