// ---------- Mixins ----------

@mixin hide($timing) {
  opacity: 0;
  pointer-events: none;
  user-select: none;
  transition: opacity $timing;
}

@mixin show() {
  opacity: 1;
  pointer-events: auto;
  user-select: auto;
}

// ---------- Keyframes ----------

@keyframes ripple{
  0%{opacity: 1; transform: scale(1)}
  33%{opacity: 0; transform: scale(1.4)}
  100%{opacity: 0; transform: scale(1.4)}
}

// ---------- Vars ----------

$white: #ffffff;
$black: #000000;
$beige: #dad3c5;
$darkBeige: #868177;

